/**
 *  
 * */
export default {
	methods: {
		/**
		 * 
		 */
		_event_link(eventData) {
			return new Promise((resolve) => {
				if(eventData.url){
					window.location.href = eventData.url;
				}
				resolve()
			})
		},
		/**
		 * 
		 * @private
		 */
		_event_share() {
			return new Promise((resolve) => {
				window.alert('')
				resolve()
			})
		},
		/**
		 * 
		 * @private
		 */
		_event_submitForm(){
			return new Promise((resolve) => {
				window.alert('...')
				resolve()
			})
		}
	}
}
